<template>
  <div>
    <Header></Header>
    <div style="margin-left: 11%;">
      <HR style="margin-top: 50px; FILTER: alpha (opacity = 100, finishopacity =0 , style= 3 )" width="80%">
      <div style="margin-top: 40px;margin-bottom: 500px;margin-left: 30px;">
        <el-col :span="14">
          <div id="pieReport" style="width: 550px;height: 400px;"></div>
        </el-col>
        <!-- <el-col :span="1">
          <table border="1px" cellpadding="0" cellspacing="0"
            style=" margin-left :200 px ;height : 375px ; border-left-style: solid ; border-bottom-style : none; border-right-style :none ; border-top-style: none ">
          </table>
        </el-col> -->
        <el-col :span="9">
          <div class="seven_echarts" id="seven" style="width: 400px;height: 350px;margin-top: 20px;"></div>
        </el-col>
      </div>
      <HR style=" FILTER: alpha (opacity = 100, finishopacity =0 , style= 3 )" width="80%">
    </div>
  </div>
</template>

<script>
  import echarts from 'echarts'
  import Header from '../general/Header'
  import get_url from '../general/getUrl'
  import $ from 'jquery'

  export default {
    data() {
      return {
        gpalist:null,
        coursenum: 10,
        opinion: ["60", "60~70", "70~80", "80~90", "90"],
        opinionData: [{
            value: 12,
            name: "60"
          },
          {
            value: 18,
            name: "60~70"
          },
          {
            value: 12,
            name: "70~80"
          },
          {
            value: 12,
            name: "80~90"
          },
          {
            value: 12,
            name: "90"
          },
        ],
        seven_chart: null,
        month_chart: null,
        
      }
    },
    components: {
      Header
    },
    mounted() {
      var self = this
      var post_url = get_url(this.$store.state.dev, '/score/')
      var post_data = {'userid':this.$store.state.userid}
      $.ajax({
        ContentType: 'application/json; charset=utf-8',
        dataType: 'json',
        url: post_url,
        type: 'POST',
        data: post_data,
        async: false,
        success: function (data) {
          self.gpalist = data['gpa']
          self.opinionData= data['studentscore']
          self.coursenum= data['coursenum']
        },
        error: function () {
          alert('')
        }
      })
      this.drawPie();
      this.get_echarts();
    },
    methods: {
      get_echarts: function () {
        this.seven_chart = echarts.init(document.getElementById("seven"));
        // 
        this.seven_chart.setOption(
        {
          title: {
            text: 'GPA',
            x: 'left',
            align: 'center'
          },
          tooltip: {
            trigger: 'axis'
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          toolbox: {
            feature: {
              magicType: {
                type: ['line', 'bar']
              }, //
              restore: {}, //
              saveAsImage: {}, //
            }
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: ["", "", "", "", "", "", ""]
          },
          yAxis: {
            name: "GPA",
            nameLocation: 'end',
            type: 'value',
            axisLabel: {
              formatter: '{value} '
            }
          },
          series: [{
              name: 'GPA',
              type: 'line',
              data: this.gpalist,
              lineStyle: { //
                color: '#3f89ec'
              },
              itemStyle: { //
                normal: {
                  color: '#3f89ec'
                }
              }
            },


          ],
        }
        )
      },
      drawPie: function () {
        var mycharts = echarts.init(document.getElementById("pieReport"));
        // this.charts =  echarts.init(this.$refs.pieReport)
        mycharts.setOption({
          title: {
            text: '',
            subtext: '' + this.coursenum,
            // x 'center'  'left'  'right'  {number}xpx
            x: 'center',
            // y 'top'  'bottom'  'center'  {number}ypx
            y: 'top',
            // itemGappx10
            // itemGap: 30,
            // backgroundColor: '#EEE',
            // 
            textStyle: {
              fontSize: 20,
              fontWeight: 'bolder',
              // color: '#000080'
            },
            // 
            subtextStyle: {
              fontSize: 12,
              color: 'grey'
            }
          },
          tooltip: {
            trigger: "item",
            formatter: "{a}<br/>{b}:{c} ({d}%)"
          },
          legend: {
            bottom: 10,
            left: "center",
            data: this.opinion
          },
          series: [{
            name: "",
            type: "pie",
            radius: "60%",
            center: ["50%", "50%"],
            avoidLabelOverlap: false,
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              },
              color: function (params) {
                //
                var colorList = ["rgb(46, 57, 58)", "#79d2c0", "rgb(28, 147, 168)", "rgb(179, 91, 119)",
                  "crimson"
                ];
                return colorList[params.dataIndex];
              }
            },
            data: this.opinionData,
            label: {
              normal: {
                // position: 'inner',  //  'outer'  'inner'
                // formatter: '{a} {b} : {c} ({d}%)'    {b}
                // {a}series.name  {b}series.dataname
                // {c}series.datavalue  {d}%
                formatter: '{b}: {c} ({d}%)'
              }
            }
          }]
        });
      },
    }
  }

</script>

<style type="text/css">
  .content {
    width: 100%;
  }

  .content p {
    margin-top: 1rem;
    font-size: 0.4rem;
    color: #666666;
  }

  .seven_echarts {
    height: 11rem;
    width: 94%;
  }

</style>



