<template>
  <div id="personal">
    <el-col :span="24" class="header">
      <el-col :span="12" style="text-align:right;font-size:30px;font-weight:bold;">{{ nickname }}</el-col>
      <el-col :span="12" style="text-align:left;font-size:20px;font-style:italic;">
        <p>{{ signature }}</p>
      </el-col>
    </el-col>
    <el-col :span="2" :offset="1">
      <el-menu @select="handle_select">
        <el-menu-item index="index"></el-menu-item>
        <el-menu-item index="1"></el-menu-item>
        <el-menu-item index="2" @click.native="test"></el-menu-item>
        <el-menu-item index="3" @click.native="test"></el-menu-item>
        <el-menu-item index="4" @click.native="test"></el-menu-item>
        <el-menu-item index="5" @click.native="test"></el-menu-item>
      </el-menu>
    </el-col>

    <el-col :span="14" style="margin:30px 0px 0px 0px;">
      <el-row>
        <el-col :span="7" :offset="1">
          <!-- <div id="headPortrait"><img :src="headPortrait" style="height:200px;"></img></div> -->
          <div class="hello">
            <div class="user-header">
              <input type="file" name="image" accept="image/*" @change='onchangeImgFun'
               class="header-upload-btn user-header-com" style="width: 280px;height: 280px;display: inline-block;">
              <img  alt="" :src='imgStr' class="user-header-img user-header-com" style="width: 280px;height: 280px;display: inline-block;">
              <p class="tip">5MB <span class="error">{{errorStr}}</span></p>
            </div>
          </div>
        </el-col>
        <el-col :span="14" :offset="2">
          <el-row style="margin:10px 0px 20px 0px;">
            <el-col :span="16">
              <el-row>
                <el-col :span="5">:</el-col>
                <el-col :span="16">{{ username }}</el-col>
              </el-row>
              <el-row>
                <el-col :span="4">:</el-col>
                <el-col :span="16">{{ nickname }}</el-col>
              </el-row>
              <el-row>
                <el-col :span="4">:</el-col>
                <el-col :span="20">{{ gender }}</el-col>
              </el-row>
              <el-row>
                <el-col :span="4">:</el-col>
                <el-col :span="20">{{ college }}</el-col>
              </el-row>
              <el-row v-if="is_host">
                <el-col :span="4">:</el-col>
                <el-col :span="20"> {{ email }} </el-col>
              </el-row>
              <el-row v-if="is_host&&!is_superuser">
                <el-col>
                  <span style="color:red"></span>
                  <el-button type="button" @click="email_check"></el-button>
                </el-col>
              </el-row>
              <el-row v-if="is_host">
                <el-col :span="4">:</el-col>
                <el-col :span="20"> {{ stunum }} </el-col>
              </el-row>
              <el-row v-if="is_host&&!is_ynu">
                <el-col>
                  <span style="color:red"></span>
                  <el-button type="button" @click="stunum_check"></el-button>
                </el-col>
              </el-row>
              <!--el-row>
              <el-col :span="4">:</el-col>
              <el-col :span="6">{{ follows }}</el-col>
              <el-col :span="4">:</el-col>
              <el-col :span="6">{{ fans }}</el-col>
            </el-row-->
            </el-col>
            <el-col :span="8">
              <el-button type="button" @click="edit_info_button_clicked" v-if="is_host"><i class="el-icon-edit"></i>
                
              </el-button>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="6">:</el-col>
          </el-row>
          <el-row>
            <el-col :span="24">{{ personalIntro }}</el-col>
          </el-row>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="22" :offset="1" style="background-color:green;">

        </el-col>
      </el-row>
    </el-col>

    <el-col :span="6" style="margin:30px 0px 0px 0px;">
      <el-col id="tableTitle">
        {{ username }}
      </el-col>
      <el-table :data="tableData" stripe border style="width:100%">
        <el-table-column prop="course" label="" :span="12">
        </el-table-column>
        <el-table-column prop="points" label="" :span="12">
        </el-table-column>
      </el-table>
    </el-col>
    <el-dialog title="" :visible.sync="dialog_visible">
      <el-form :model="form" label-position="top" ref="form" :rules="form_rules">
        <el-form-item label="" type="text" :label-width="form_label_width" prop="nickname" required>
          <el-input v-model="form.nickname" auto-complete="off" placeholder=",20,"></el-input>
        </el-form-item>
        <el-form-item type="select" label="" :label-width="form_label_width" required>
          <el-select v-model="form.gender" placeholder="">
            <el-option v-for="item in gender_options" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item type="select" label="" :label-width="form_label_width" required>
          <el-select v-model="form.college_id" placeholder="">
            <el-option v-for="item in academy_options" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item type="text" label="" :label-width="form_label_width" prop="intro">
          <el-input v-model="form.intro" auto-complete="off" placeholder="50"></el-input>
        </el-form-item>

      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click.native="edit_confirm_clicked('form')"> </el-button>
        <el-button @click.native="dialog_visible=false"> </el-button>
      </span>
    </el-dialog>

    <el-dialog title="" :visible.sync="isynu_visible">
      <el-form :model="ynuform" label-position="top" ref="ynuform">
        <el-form-item label="" type="text" :label-width="form_label_width" prop="username" required>
          <el-input v-model="ynuform.username" auto-complete="off" placeholder=""></el-input>
        </el-form-item>

        <el-form-item label="" type="password" :label-width="form_label_width" prop="password" required>
          <el-input v-model="ynuform.password" type="password" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click.native="stunum_cinfirm_clicked('ynuform')"> </el-button>
        <el-button @click.native="isynu_visible=false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  /* eslint-disable brace-style */
  /* eslint-disable camelcase */
  import $ from 'jquery'
  import hp from './../assets/headportrait.jpg'
  import get_url from './general/getUrl.js'
  import college_map from './general/collegeMap.js'
  export default {
    name: 'personal',
    data() {
      var check_nickname = (rule, value, callback) => {
        setTimeout(() => {
          var l = value.length
          var rl = 0
          for (var i = 0; i < l; i++) {
            if ((value.charCodeAt(i) & 0xff00) !== 0) {
              rl++
            }
            rl++
          }
          if (rl >= 1 && rl <= 20) {
            callback()
          } else {
            callback(new Error('20'))
          }
        }, 500)
      }
      var check_intro = (rule, value, callback) => {
        setTimeout(() => {
          var l = value.length
          var rl = 0
          for (var i = 0; i < l; i++) {
            if ((value.charCodeAt(i) & 0xff00) !== 0) {
              rl++
            }
            rl++
          }
          if (rl >= 1 && rl <= 50) {
            callback()
          } else {
            callback(new Error('50'))
          }
        }, 500)
      }

      return {
        imgStr: this.$store.state.headimg,
        errorStr: '',
        email: '',
        is_superuser: false,
        is_host: false,
        is_ynu: false,
        isynu_visible: false,
        stunum: '',
        spaceName: this.username + '',
        signature: '',
        username: '',
        nickname: '',
        gender: '',
        college: '',
        follows: 0,
        fans: 0,
        personalIntro: '',
        tableData: [{
          course: '',
          points: ''
        }],
        headPortrait: hp,
        dialog_visible: false,
        form_label_width: '80px',
        form: {
          nickname: '',
          gender: '',
          intro: '',
          college_id: ''
        },
        ynuform: {
          username: '',
          password: ''
        },
        gender_options: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }, {
          value: '0',
          label: ''
        }],
        academy_options: [{
            label: '1',
            value: 1
          },
          {
            label: '2',
            value: 2
          },
          {
            label: '3',
            value: 3
          },
          {
            label: '4',
            value: 4
          },
          {
            label: '5',
            value: 5
          },
          {
            label: '6',
            value: 6
          },
          {
            label: '7',
            value: 7
          },
          {
            label: '8',
            value: 8
          },
          {
            label: '9',
            value: 9
          },
          {
            label: '10',
            value: 10
          },
          {
            label: '11',
            value: 11
          },
          {
            label: '12',
            value: 12
          },
          {
            label: '13',
            value: 13
          },
          {
            label: '14',
            value: 14
          },
          {
            label: '15',
            value: 15
          },
          {
            label: '16',
            value: 16
          },
          {
            label: '17',
            value: 17
          },
          {
            label: '18',
            value: 18
          },
          {
            label: '19',
            value: 19
          },
          {
            label: '20',
            value: 20
          },
          {
            label: '21',
            value: 21
          },
          {
            label: '23',
            value: 23
          },
          {
            label: '24',
            value: 24
          },
          {
            label: '25',
            value: 25
          },
          {
            label: '26',
            value: 26
          },
          {
            label: '27',
            value: 27
          },
          {
            label: '28',
            value: 28
          },
          {
            label: '29',
            value: 29
          },
          {
            label: '30',
            value: 30
          },
          {
            label: '32',
            value: 32
          },
          {
            label: '33',
            value: 33
          },
          {
            label: '51',
            value: 51
          },
          {
            label: '52',
            value: 52
          },
          {
            label: '56',
            value: 56
          },
          {
            label: '91',
            value: 91
          }
        ],
        form_rules: {
          nickname: [{
            validator: check_nickname,
            trigger: 'blur'
          }],
          intro: [{
            validator: check_intro,
            trigger: 'blur'
          }]
        }
      }
    },
    methods: {
      onchangeImgFun (e) {
        var file = e.target.files[0]
        console.log(file)
        // 
        let imgSize = file.size
        console.log(imgSize)
        var _this = this // this
        // 5Mb
        if (imgSize <= 50 *1024* 1024) {
          // 
          _this.errorStr = ''
          console.log('')
          // 
          //  1
          // this.imgStr = window.URL.createObjectURL(e.target.files[0])
          // console.log(window.URL.createObjectURL(e.target.files[0])) // 

          // base64 2
          var reader = new FileReader()
          reader.readAsDataURL(file) //  base64
          reader.onloadend = function () {
            //  base64 ,  img  src 
            var dataURL = reader.result
            _this.$store.state.headimg = dataURL
            console.log(dataURL)
            _this.imgStr = dataURL
            // 
          }
        } else {
          console.log('')
          _this.errorStr = ''
        }
      },
      email_check: function () {
        var post_url = get_url(this.$store.state.dev, '/sign/emailcheck/')
        var _this = this
        $.ajax({
          ContentType: 'application/json; charset=utf-8',
          dataType: 'json',
          url: post_url,
          type: 'POST',
          success: function (data) {
            var code = data['error']
            if (code === 0) {
              _this.$message({
                showClose: true,
                type: 'success',
                message: ''
              })
            } else {
              _this.$message({
                showClose: true,
                type: 'error',
                message: ''
              })
            }
          },
          error: function () {
            _this.$message({
              showClose: true,
              type: 'error',
              message: ''
            })
          }
        })
      },
      test: function () {
        alert('')
      },

      stunum_cinfirm_clicked: function (form_name) {
        this.$refs[form_name].validate((valid) => {
          var post_url = get_url(this.$store.state.dev, '/studentnotifications/')
          var post_data = {
            'username': this.ynuform.username,
            'password': this.ynuform.password,
            'id': this.$store.state.userid
          }
          $.ajax({
            ContentType: 'application/json; charset=utf-8',
            dataType: 'json',
            url: post_url,
            type: 'POST',
            data: post_data,
            success: function (data) {
              var _this = this
              var code = data['result']
              if (code) {
                _this.isynu_visible = false
                _this.$store.state.is_ynu = true
                _this.is_ynu = true
                _this.stunum = this.ynuform.username
                _this.$router.go(0)
              } else {
                _this.$message({
                  showClose: true,
                  type: 'error',
                  message: ''
                })
              }
            },
            error: function () {
              this.$message({
                showClose: true,
                type: 'error',
                message: ''
              })
            }
          })
        })
      },

      edit_confirm_clicked: function (form_name) {
        this.$refs[form_name].validate((valid) => {
          var post_url = get_url(this.$store.state.dev, '/user/modify/info/')
          var post_data = {
            nickname: this.form.nickname,
            gender: this.form.gender,
            intro: this.form.intro,
            college_id: this.form.college_id
          }
          var _this = this
          $.ajax({
            ContentType: 'application/json; charset=utf-8',
            dataType: 'json',
            url: post_url,
            type: 'POST',
            data: post_data,
            success: function (data) {
              var code = data['error']
              if (code === 0) {
                _this.dialog_visible = false
                _this.$router.go(0)
              } else if (code === 1) {
                _this.$message({
                  showClose: true,
                  type: 'error',
                  message: ''
                })
              }
            },
            error: function () {
              _this.$message({
                showClose: true,
                type: 'error',
                message: ''
              })
            }
          })
        })
      },
      edit_info_button_clicked: function () {
        this.dialog_visible = !this.dialog_visible
      },
      stunum_check:function(){
        this.isynu_visible = !this.isynu_visible
      },
      handle_select: function (key, key_path) {
        if (key === 'index') {
          this.$router.push({
            path: '/index'
          })
        }
      }
    },
    created: function () {
      for (var i = 0; i < this.academy_options.length; i++) {
        var college = this.academy_options[i]
        if (college_map[college['value']] !== undefined) {
          // alert(college_map[college['value']])
          this.academy_options[i]['label'] = college_map[college['value']]
        }
      }
      this.username = this.$route.params.username
      this.is_ynu = this.$store.state.is_ynu
      var personalSelf = this
      var post_url = get_url(this.$store.state.dev, '/sign/logged_in/')
      $.ajax({
        ContentType: 'application/json; charset=utf-8',
        dataType: 'json',
        url: post_url,
        type: 'POST',
        async: false,
        success: function (data) {
          personalSelf.is_host = (personalSelf.username === data['username'])
        },
        error: function () {
          alert('')
        }
      })
      post_url = get_url(this.$store.state.dev, '/user/information/')
      $.ajax({
        ContentType: 'application/json; charset=utf-8',
        dataType: 'json',
        url: post_url,
        type: 'POST',
        data: {
          'username': personalSelf.username
        },
        async: false,
        success: function (data) {
          var user_info = data['user_info']
          personalSelf.username = user_info['username']
          personalSelf.nickname = user_info['nickname']
          personalSelf.form.nickname = user_info['nickname']
          personalSelf.form.gender = user_info['gender']
          if (user_info['gender'] === '1') {
            personalSelf.gender = ''
          } else if (user_info['gender'] === '2') {
            personalSelf.gender = ''
          } else if (user_info['gender'] === '0') {
            personalSelf.gender = ''
          } else {
            personalSelf.gender = ''
          }
          personalSelf.form.intro = user_info['intro']
          personalSelf.personalIntro = user_info['intro']
          var college_id = user_info['college_id']
          var college_name = ''
          if (college_id !== null) {
            college_name = (college_map.hasOwnProperty(college_id)) ? college_map[college_id] : college_id
              .toString() + ''
          }
          personalSelf.college = college_name
          personalSelf.form.college_id = college_id
          personalSelf.email = user_info['email']
          personalSelf.is_superuser = user_info['is_superuser']
          personalSelf.stunum = user_info['studentid'] // 
          if(user_info['is_ynu']==='1'){
            personalSelf.is_ynu = true
          }
          else{
            personalSelf.is_ynu = false
          }
          
        },
        error: function () {
          alert('fail')
        }
      })
    }
  }

</script>

<style scpoed>
  .user-header{
    position: relative;
    display: inline-block;
  }
  /* .user-header-com{ */
    /* width: 200px;
    height: 200px; */
    /* display: inline-block; */
  /* } */
  .header-upload-btn{
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0;
    /* inputimg */
  }
  .tip{
    font-size: 14px;
    color: #666;
  }
  .header {
    height: 60px;
    line-height: 60px;
    background: #20a0ff;
    color: #fff;
    text-align: center;
    font-size: 20px;
  }

  .el-menu-item {
    color: black;
    font-size: 14px;
  }

  .el-row {
    margin-bottom: 10px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .el-col {
    border-radius: 0px;
  }

  #headPortrait {
    height: 200px;
    font-size: 20px;
    text-align: center;
    line-height: 200px;
  }

  #tableTitle {
    font-weight: bold;
    text-align: center;

    height: 40px;
    font-size: 20px;
  }

</style>



